.Todo-Items{
    cursor: pointer;
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 24px;
    border-radius: 12px;
}

.Todo-Items:hover{
    box-shadow: 0px 5px 50px rgba(97, 218, 250, 0.3);
}

.TodoItem-p{
    
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    /* text-align: center; */
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}