.Todo-Add{
    width: 90%;
    max-width: 300px;
    background-color: #FAFAFA;
    padding: 32px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
}

label{
    text-align: center;
    font-weight: bold;
    font-size: 20PX;
    color: #1E1E1F;
    margin-bottom: 12px;
}

textarea{
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 8px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px 20px;
    height: 96px;
    width: calc(100% - 25px);
    resize: none;
}

textarea::placeholder{
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}

textarea:focus{
    outline-color: #0794CC;
}

.TodoForm-buttonContainer{
    margin-top: 12px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoAdd-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 4px;
    border: none;
    font-family: 'Monserrat';
}

.TodoAdd-button--add{
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoAdd-button--cancel{
    background: transparent;
}

